<template>
	<div id="menu_list_container">
		<div class="menu-item-card" v-for="(item, index) in menuData" :key="index" @click="toDetail(item)">
			<img :src="item.icon.value" class="menu-icon" />
			<div class="menu-title">{{item.menuName}}</div>
		</div>

	</div>
</template>

<script setup>
	import {useRouter} from 'vue-router';
	import menuData from '@/mock/menu/data';
	
	const router = useRouter();
	const toDetail = ({
		path
	}) => {
		if (path) {
			router.push({
				path
			});
		} else {
			alert('功能正在开发中，敬请期待');
		}

	}
</script>

