			height: 100px;
			padding: 6px 48px;
			border-radius: 12px;
			background-color: rgba(0, 140, 140, 0.75);
			display: flex;

			&:nth-of-type(even) {
				.menu-icon {
					flex-shrink: 0;
					display: block;
					height: 100%;
				}

				.menu-title {
					flex-grow: 1;
					font-size: 30px;
					color: #8f4b28;
